.method-bank {
  padding-top: 30px;

  h1 {
    font-weight: bold;
    font-size: 40px;
    margin-bottom: 30px;

    @media (max-width: 767px) {
      font-size: 24px;
    }
  }

  h2 {
    font-weight: bold;
    font-size: 30px;

    @media (max-width: 767px) {
      font-size: 18px;
    }
  }

  h3 {
    font-size: 24px;
    font-weight: bold;
    margin: 20px 0;

    @media (max-width: 767px) {
      font-size: 16px;
    }
  }

  li {
    &:not(:last-child) {
      margin-bottom: 5px;
    }
  }

  a {
    color: @header-background;
    transition: all 0.6s ease-in-out;

    &:hover {
      opacity: 0.6;
    }

    @media (max-width: 767px) {
      font-size: 14px;
    }
  }
}
